<template>
  <div>

    <Loader v-if="isLoading" />
    <div v-else>
      <table class="table_blur">
        <tr>
          <th class="photo_td"></th>
          <th>Nickname</th>
          <th>Email</th>
          <th>Admin</th>
          <th>Action</th>
        </tr>
        <tr v-for="user in users" :key="user.id">
          <td class="photo_td"><img :src="user.photo" alt="" class="photo"></td>
          <td>{{user.nickname}}</td>
          <td>{{user.email}}</td>
          <td><input type="checkbox" v-model="user.admin" /></td>
          <td>
            <div class="line">
            <div class="pencil_ico inline" @click="editUser(user.id)"></div>
            <div class="trash_ico inline" @click="deleteUser(user.id)"></div>
            </div>
          </td>
        </tr>
      </table>
    </div>
    
  </div>
</template>

<script>
import { mapGetters, mapMutations } from 'vuex';
import Loader from '@/components/Loader';

export default {
  name: 'Users',
  components: { Loader },
  data () {
    return {
      users: []
    }
  },
  methods: {
    ...mapMutations(['changeIsLoading']),
    getData() {
      this.changeIsLoading(true);

      const sended_data = {};

      const url = 'get_all_users';
      this.$http.post(url, sended_data, { withCredentials: true })

      .then(response => { return response.json(); })
      .then(data => {
        this.users = data.users;
        this.changeIsLoading(false);
      })
      .catch((err) => {
        console.log(err)
      })
    }
  },
  computed: {
    ...mapGetters(['isLoading']),
  },
  mounted() {
    this.getData();
  }
}
</script>

<style lang="scss" scoped>

.photo {
  height: 2.5rem;
  width: 2.5rem;

  border-radius: 100px;
  border: 1px solid rgb(128, 128, 128);
  box-shadow: 0 0 1px #666;
}

.photo_td {
  width: 2.5rem;
}

.table_blur {
  background: #f5ffff;
  border-collapse: collapse;
  text-align: left;

  width: 40rem;

  margin-left: auto;
  margin-right: auto;

  margin-top: 2rem;
}
.table_blur th {
  // border-top: 1px solid #777777;	
  // border-bottom: 1px solid #777777; 
  box-shadow: inset 0 1px 0 #999999, inset 0 -1px 0 #999999;
  // background: #AFCDE7;
  // color: white;
  padding: 10px 15px;
  position: relative;

  text-align: center;
}
.table_blur th:after {
  content: "";
  display: block;
  position: absolute;
  left: 0;
  top: 25%;
  height: 25%;
  width: 100%;
  // background: linear-gradient(rgba(255, 255, 255, 0), rgba(255,255,255,.08));
}
.table_blur tr:nth-child(odd) {
  background: #ebf3f9;
}
// .table_blur th:first-child {
//   // border-left: 1px solid #777777;	
//   // border-bottom:  1px solid #777777;
//   box-shadow: inset 1px 1px 0 #999999, inset 0 -1px 0 #999999;
// }
// .table_blur th:last-child {
//   // border-right: 1px solid #777777;
//   // border-bottom:  1px solid #777777;
//   box-shadow: inset -1px 1px 0 #999999, inset 0 -1px 0 #999999;
// }
.table_blur td {
  border: 1px solid #ccc;//#e3eef7;
  padding: 10px 15px;
  position: relative;
  transition: all 0.5s ease;
}
.table_blur tbody:hover td {
  color: transparent;
  text-shadow: 0 0 3px #a09f9d;
}
.table_blur tbody:hover tr:hover td {
  color: #444444;
  text-shadow: none;
}

.pencil_ico {
  margin: auto;
  margin-left: 0.7rem;
  background: url('../assets/img/SVG/pencil.svg') no-repeat center;
  background-size: contain;
}

.trash_ico {
  margin: auto;
  margin-left: 0.7rem;
  background: url('../assets/img/SVG/trash.svg') no-repeat center;
  background-size: contain;
}

.pencil_ico, .trash_ico {
  width: 1rem;
  height: 1rem;
}

.line {
  display: flex;
  flex-direction: row;
}

.inline {
  display: inline-block;
}

</style>



